import { useTranslation } from "react-i18next";
import { useCallback, useContext } from "react";
import { useSearchParams } from "react-router-dom";

import classes from "./Buildings.module.scss";
import useBuildingsColumns from "./BuildingsColumns";
import { CommonInfoContext } from "context/commonInfo";
import { DownloadXls, Pagination, Table } from "components/shared/AntComponents";

type Props = {};

const data = Array(10).fill({
	id: 1,
	a: "Олой сити",
	b: 1,
	c: 2,
	d: 3,
	e: 4
});

const Buildings = (props: Props) => {
	const { t } = useTranslation();
	const columns = useBuildingsColumns();
	const [search, setSearch] = useSearchParams();
	const {
		state: { page, size }
	} = useContext(CommonInfoContext);

	const onChangePagination = useCallback(
		(p: number) => {
			search.set("page", String(p - 1));
			setSearch(search);
		},
		[page, search]
	);

	return (
		<div className={classes.main}>
			<Table rowKey={"id"} loading={false} columns={columns} dataSource={data} />

			<div className="table-bottom mt-30">
				<Pagination total={50} pageSize={Number(size)} current={Number(page) + 1} onChange={onChangePagination} />

				<DownloadXls className="ml-auto" />
			</div>
		</div>
	);
};

export default Buildings;
